/* You can add global styles to this file, and also import other style files */
html,
body {
  height: 100%;
  width: 100%;
  margin: 0;
  padding: 0;
  background-color: #222;
  font-family: monospace;
}

html {
  overflow: hidden;
}

html{     
    -webkit-touch-callout: none;
    -webkit-user-select: none;
    -khtml-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;  
}

.white-text-shadow {
  color: white;
  text-shadow: 5px 5px 5px black;
}

.container {
  border-bottom: 1px solid #565b73;
  margin: 10%;
  background-color: rgba(31, 36, 60, 0.5);

}

.text-container {
  padding: 5%;
}

p {
  line-height: 1.4em;
  color: #ccc;
}

a {
	text-decoration: none;
}


.small-font-size {
  font-size: 1vw;
}

.normal-font-size {
  font-size: 1.5vw;
}

.medium-font-size {
  font-size: 2vw;
}

.large-font-size {
  font-size: 2.5vw;
}

.big-font-size {
  font-size: 3vw;
}

.huge-font-size {
  font-size: 5vw;
}

.text-align-center {
  text-align: center;
}

.text-align-left {
  text-align: left;
}

.text-align-right {
  text-align: right;
}

/** DOTS HR */
.hr-block-separator.is-style-dots {
  background: none !important;
  border: none;
  text-align: center;
  max-width: none;
  line-height: 1;
  height: auto;

  color: white;
  text-shadow: 5px 5px 5px black;
}

.hr-block-separator.is-style-dots:before {
  content: "\00b7 \00b7 \00b7";
  color: currentColor;
  font-size: 20px;
  letter-spacing: 2em;
  padding-left: 2em;
}

/**NEON HR */
.hr {
  display: table;
  width: 100%;
  margin: 0 auto;
  padding: 0;
}

.hr .inner {
  display: table-cell;
  vertical-align: middle;
  text-align: center;
}

.hr .line {
  width: 100%;
  display: table-cell;
  vertical-align: middle;
}

.hr .line .stroke {
  height: 4px;
  border-top: 2px solid #003b59;
  border-bottom: 2px solid #003b59;
  background-color: #e8e8e8;
  width: 100%;
}


.alignleft {
  display: inline;
  float: left;
  margin-right: 1.5em;
}

.alignright {
  display: inline;
  float: right;
  margin-left: 1.5em;
}

.aligncenter {
  clear: both;
  display: block;
  margin-left: auto;
  margin-right: auto;
}

/* 
      }
    ##Device = Desktops
    ##Screen = 1020px to higher resolution desktops
    */
@media (min-width: 1020px) {
  #wrap {}

  .ball {
    pointer-events: none;
    position: absolute;
    width: 10px;
    height: 10px;
    border-radius: 0;
    background: gray;
    animation: implode 1s ease-in-out;
    animation-fill-mode: both;
    opacity: 0.1;
  }

  @keyframes implode {
    100% {
      transform: scale(0);
    }
  }

  
}

/* 
    ##Device = Tablets, Ipads (portrait)
    ##Screen = B/w 768px to 1024px
  */

@media (min-width: 768px) and (max-width: 1024px) {

  .ball,
  .face,
  .eye {
    display: none;
  }
}

/* 
    ##Device = Tablets, Ipads (landscape)
    ##Screen = B/w 768px to 1024px
  */

@media (min-width: 768px) and (max-width: 1024px) and (orientation: landscape) {

  .ball,
  .face,
  .eye {
    display: none;
  }

 
}

/* 
    ##Device = Low Resolution Tablets, Mobiles (Landscape)
    ##Screen = B/w 481px to 767px */

@media (min-width: 481px) and (max-width: 767px) {

  .ball,
  .face,
  .eye {
    display: none;
  }

  
}

/* 
    ##Device = Most of the Smartphones Mobiles (Portrait)
    ##Screen = B/w 320px to 479px
  */

@media (min-width: 320px) and (max-width: 480px) {

  .ball,
  .face,
  .eye {
    display: none;
  }


  .small-font-size {
    font-size: 2vw;
  }

  .normal-font-size {
    font-size: 2.5vw;
  }

  .medium-font-size {
    font-size: 3vw;
  }

  .large-font-size {
    font-size: 3.5vw;
  }

  .big-font-size {
    font-size: 4vw;
  }

  .huge-font-size {
    font-size: 6vw;
  }

}
